.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.friendlist {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.item {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 200px;
  height: 50px;
  box-shadow: 1px 2px 5px 0px rgba(0, 0, 0, 1);
  border-radius: 5px;
}

.avatar {
  margin-left: 10px;
}

.name {
  font-weight: 500;
  margin-left: 10px;
}

.online {
  margin-left: 10px;
  display: flex;
  align-items: center;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: green;
}

.offline {
  margin-left: 10px;
  display: flex;
  align-items: center;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: red;
}
